import { useState } from "react";
import { useTodoDispatch } from "../context/TodoContext.js";
import { todoApi } from "../Api/TodoApi.js";

const Item = ({ todo }) => {

  const [ editingContent, setEditingContent ] = useState(todo.content);
  const dispatch = useTodoDispatch();

  const complete = _ => {
    todoApi.delete(todo)
    .then(_ => {
      dispatch({ type: "todo/delete", todo });
    });
  };

  const toggleEditing = _ => {
    const newTodo = { ...todo, editing: !todo.editing }
    todoApi.update(newTodo)
    .then(response => {
      const newEditing = Boolean(response[0].editing);
      console.log(newEditing);
      const newTodo = { ...response[0], editing: newEditing };
      dispatch({ type: "todo/update", todo: newTodo });
    });
  };

  const confirmUpdate = e => {
    e.preventDefault();
    const newTodo = { ...todo, content: editingContent, editing: !todo.editing };
    todoApi.update(newTodo)
    .then(response => {
      const newEditing = Boolean(response[0].editing);
      console.log(newEditing);
      const newTodo = { ...response[0], editing: newEditing };
      dispatch({ type: "todo/update", todo: newTodo });
    });
  };

  return (
    <div key={ todo.id } className="todo">
      <button 
        type="button"
        onClick={ complete }
      >complete</button>
      <form
        className="update-content-form"
        onSubmit={ e => confirmUpdate(e) }
      >
        {
          todo.editing 
            ? <input
               type="text"
               value={ editingContent }
               onChange={ e => setEditingContent(e.target.value) }
               style={{ marginLeft: "5px" }}
              />
            : <span onDoubleClick={ toggleEditing }>{ todo.content }</span>
        }
      </form>
    </div>
  );
};

export default Item;